
.daily_theme{
  margin-top: -120px;
}
.detail-img-box {
  position: relative;
  z-index: 0;
  height: 230px;
  width: 100%;
  background-size: 100%;
  background-position: center;
  background-repeat: no-repeat;
}
.detail-mask{
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-image: -webkit-gradient(linear, left top, left bottom, from(bottom), color-stop(0%, rgba(0,0,0,0.7)), color-stop(40%, rgba(0,0,0,0.1)), to(rgba(0,0,0,0.1)));
    background-image: linear-gradient(bottom, rgba(0,0,0,0.7) 0%, rgba(0,0,0,0.1) 40%, rgba(0,0,0,0.1) 100%);
}
.detail-title{
    position: absolute;
    bottom: 23px;
    line-height: 1.2;
    left: 0;
    padding: 0 18px;
    font-weight: 300;
    font-size: 21px;
    color: #ffffff;
}
.detail-image-source{
    position: absolute;
    bottom: 8px;
    right: 0;
    padding: 0 18px;
    font-weight: 300;
    font-size: 12px;
    color: #d3d3d3;
}
.list-box{
  background: #f2f2f2;
}
.editors-box {
  background: #efefef;
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  padding-left: 50px;
}
.editors-box p{
    position: absolute;
    top: 13px;
    left: 15px;
    font-size: 13px;
    font-weight: bold;
}
.editors-box .editors-item {
  height: 30px;
  width: 30px;
  margin: 7px;
}
.editors-item >img{
      height: 30px;
      width: 30px;
      border-radius: 50%;
}



