
.detail_bg{
  width:100%;
  height:150px;
}
.select{
  color:#ffcf00;
}
.header-icon span{
  position:absolute;
  left:32px;
  bottom:-2px;
  font-size:12px;
}
.detail-content{
  padding:20px;
  font-size:15px;
  color:#777;
}
.question-title{
  font-size:18px;
  color:#333;
}
.detail-content .meta{
  margin-top:20px;
  height:20px;
  line-height:20px;
  width:100%;
  overflow:hidden;
  text-overflow:ellipsis;
  white-space:nowrap;
}
.detail-content .question a{
  color:#00a2ea;
}
.content p{
  margin-top:20px;
}
.answer .content hr{
  margin-top:20px;
  border-top:1px solid #e4e4e4;
}
.view-more{
  margin-top:20px;
  width:100%;
  height:30px;
  line-height:30px;
  text-align:center;
  background:#f3f3f3;
}
.detail-content .comment-meta .avatar, .detail-content .meta .avatar {
  width: 20px;
  height: 20px;
  border-radius: 4px;
  vertical-align:top;
  margin-right: 5px;
}
.detail-content .author{
  color:#333;
}
.detail-content .bio{
  color:#aaa;
}
.detail-content img {
  border: 0;
  vertical-align: middle;
  color: transparent;
  font-size: 0;
}
.detail_header{
  position: relative;
  margin-top: 44px;
  height:150px;
  overflow: hidden;
}
.detail_header img{
  margin-top: -60px;
}
.detail_header .detail-mask{
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-image: -webkit-gradient(linear,left top, left bottom,from(bottom),color-stop(rgba(0,0,0,.7)),color-stop(40%, rgba(0,0,0,.1)),to(rgba(0,0,0,.1)));
  background-image: linear-gradient(bottom,rgba(0,0,0,.7),rgba(0,0,0,.1) 40%,rgba(0,0,0,.1));
  z-index: 2;
}
.detail_header .img_res{
  padding: 2px 5px;
  position: absolute;
  right:5px;
  bottom:5px;
  color: #ccc;
  background: rgba(0,0,0,.4);
}
.detail_header .title{
  position: absolute;
  left:10px;
  bottom: 16px;
  text-align: left;
  font-size: 18px;
  font-weight: 700;
  color: #fff;
  text-shadow: 2px 2px 2px rgba(0,0,0,.2);
  z-index:9;
}
.list-footer{
  position: fixed;
  left:0;
  top:0;
  width:100%;
  height:44px;
  line-height:44px;
  color: #fff;
  font-size: 20px;
  -webkit-box-shadow: 0 1px 3px rgba(0,0,0,.1);
          box-shadow: 0 1px 3px rgba(0,0,0,.1);
  background: #00a2ea;
  z-index:10;
}
.list-footer .go_back{
    position:relative;
    float: left;
    padding:0 10px;
}
.list-footer .header-controls{
  float: right;
}
.header-controls .header-icon{
  position:relative;
  float: left;
  padding:0 10px;
  margin-right: 20px;
}
.go_back{
  margin-left: 5px;
}
.iconfont{
  font-family:"iconfont";
  font-style:normal;
  -webkit-font-smoothing: antialiased;
  -webkit-text-stroke-width: 0.2px;
  -moz-osx-font-smoothing: grayscale;
}
@font-face {
  font-family: 'iconfont';
  src: url('//at.alicdn.com/t/font_x3ksci4x9zxjq0k9.eot');
  src: url('//at.alicdn.com/t/font_x3ksci4x9zxjq0k9.eot?#iefix') format('embedded-opentype'),
  url('//at.alicdn.com/t/font_x3ksci4x9zxjq0k9.woff') format('woff'),
  url('//at.alicdn.com/t/font_x3ksci4x9zxjq0k9.ttf') format('truetype'),
  url('//at.alicdn.com/t/font_x3ksci4x9zxjq0k9.svg#iconfont') format('svg');
}




