
.slide-slide{
  position: absolute;
  width:100%;
  left:0;
  top:44px;
  -webkit-transition: all .5s;
  transition: all .5s;
}
.slider{
  position: fixed;
  left: -70%;
  top:0;
  padding-top: 20px;
  width:70%;
  height:100%;
  color: #656b70;
  background: #2c3144;
  z-index: 999;
  overflow-y: scroll;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-transition: all .5s;
  transition: all .5s;
}
.side-mask{
  position: fixed;
  left:-30%;
  top:0;
  width:30%;
  height:100%;
  background: rgba(0,0,0,.2);
  z-index: -9999;
  opacity: 0;
}
.slide-show .slider{
  left:0;
}
.slide-show .daily_box{
  left:70%;
}
.slide-show .side-mask{
  z-index:99;
  left:70%;
}
.slide-show .list-header,
.slide-show .slide-slide{
  left:70%;
}
.my-icon,
.user-signin{
  padding:10px 20px;
}
.user-avatar img{
  width:30px;
  height:30px;
  border-radius: 50%;
}
.user-avatar{
  display: inline-block;
  vertical-align: top;
}
.user-name{
  display: inline-block;
  margin-left: 10px;
  font-size: 14px;
  height:30px;
  line-height:30px;
}
.my-icon{
  padding:10px 0;
  overflow: hidden;
}
.my-icon ul li{
  float: left;
  width:33%;
  text-align: center;
  font-size: 12px;
}
.my-icon ul li .iconfont{
  font-size: 18px;
}
.daily-item ul li{
  width:100%;
  padding:2px 20px ;
  height:46px;
  line-height:46px;
  font-size: 14px;
  overflow: hidden;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.daily-item ul li span{
  float: right;
}
.daily-item ul li.router-link-exact-active.router-link-active{
  background: #222;
}
.home-icon{
  font-size:22px;
  margin-right: 10px;
}
.iconfont{
  font-family:"iconfont";
  font-style:normal;
  -webkit-font-smoothing: antialiased;
  -webkit-text-stroke-width: 0.2px;
  -moz-osx-font-smoothing: grayscale;
}




